import React, { useState } from 'react';
import rawData from './data.json'
import { Input,Table, Tag } from 'antd';
import { SearchOutlined } from '@ant-design/icons';
import Fuse from 'fuse.js';

const TAG_CE = 0
const TAG_HSC = 1
const TAG_NMPA = 2
const TAG_DH = 3
const TAG_EU_REJECTED = 4
const TAG_DISTRIBUTED_BY_HK_GOV = 5

const tag_schema = {
  [TAG_CE]: {
    color: "blue",
    text: "CE"
  },
  [TAG_HSC]: {
    color: "gold",
    text: "HSC"
  },
  [TAG_NMPA]: {
    color: "green",
    text: "中國國家藥監局"
  },
  [TAG_DH]: {
    color: "purple",
    text: "衛生署"
  },
  [TAG_EU_REJECTED]: {
    color: "red",
    text: "被歐盟拒絕列入通用名單"
  },
  [TAG_DISTRIBUTED_BY_HK_GOV]: {
    color: "geekblue",
    text: "香港政府派發"
  }
}

const renderTag = indices => {
  const tags = indices.map(i => tag_schema[i])
  return  tags.length == 0 ? "🈚️" : (
    <>
      {tags.map(tag => {
        return (
          <Tag color={tag.color} key={tag.text}>
            {tag.text}
          </Tag>
        );
      })}
    </>
  )
}

const data = rawData.map(row => {
  return {
    certifications: [
      ... row.ce_certified ? [TAG_CE] : [],
      ... row.hsc_certified ? [TAG_HSC] : [],
      ... row.nmpa_certified ? [TAG_NMPA] : [],
      ... row.dh_certified ? [TAG_DH] : [],
    ],
    tags: [
      ... row.eu_rejected ? [TAG_EU_REJECTED] : [],
      ... row.distributed_by_hk_gov ? [TAG_DISTRIBUTED_BY_HK_GOV] : [],
    ],
    ...row,
  }
})

const fuse = new Fuse(data, {
  keys: ["name", "manufacturer"]
})

const columns = [
  {
    title: "名稱",
    dataIndex: "name",
    width: "120px",
    textWrap: 'word-break',
  },
  {
    title: "廠商",
    dataIndex: "manufacturer",
    width: "120px",
    textWrap: 'word-break',
  },
  {
    title: "產地",
    dataIndex: "place_of_manufacture",
    width: "30px"
  },
  {
    title: "認證",
    dataIndex: "certifications",
    render: renderTag,
    width: "30px"
  },
  {
    title: "標籤",
    dataIndex: "tags",
    render: renderTag,
    width: "30px"
  },
  {
    title: "備註",
    dataIndex: "remarks",
    width: "300px"
  }
]

function TestKitSearch() {
  const [query, setQuery] = useState("")
  const tableData = query == "" ? data : fuse.search(query).map(res => res.item)
  return (
    <div className="test-kit-search">
      <Input prefix={<SearchOutlined />} value={query} onChange={(e) => {setQuery(e.target.value)}} />
      <Table dataSource={tableData} columns={columns} pagination={false} scroll={{x: true}}/>
    </div>
  );
}

export default TestKitSearch;
